import React, { useState, Fragment } from 'react';
import {ReactComponent as Logo} from '../assets/Logo.svg';



export default function Headline() {
    return(
       <div className="header">
           <div className="container-fluid">
               <div className="row justify-content-between align-items-center">
                   <div className="col-md-4 col-6 logo__wrapper">
                       <Logo className="logo"/>
                   </div>
                   <Burger/>
               </div>
           </div>
       </div>
    )
}

function Burger(){
    const [burgerStatus, setBurgerStatus] = useState("");

    const handleClick = () => {
        setBurgerStatus(burgerStatus === "open" ? "": "open");
    }

    return(
    <Fragment>
    <div className="navigation-mobile">
        <div className={`navigation-mobile-burger ${burgerStatus}`} onClick={handleClick}>
            <span className="navigation-mobile-burger-line"></span>
            <span className="navigation-mobile-burger-line"></span>
        </div>
    </div>
     </Fragment>
    );
    
}

